$cell-size: 32px
$sprite-tile-size: 32px
$sprite-x: 8
$sprite-y: 12
$sprite-ratio: $cell-size / $sprite-tile-size * $sprite-tile-size

@mixin tile($x, $y)
  background-position: $sprite-ratio * -1 * $x $sprite-ratio * -1 * $y

@mixin creature
  &::after
    background: url(../images/tiles.png)
    background-size: $sprite-x * $sprite-ratio $sprite-y * $sprite-ratio
    content: " "
    position: absolute
    left: 0
    width: 100%
    height: 100%

@mixin dir-tile($x, $y)
  &.-down
    @include tile($x, $y)
  &.-left
    @include tile($x + 1, $y)
  &.-up
    @include tile($x + 2, $y)
  &.-right
    @include tile($x + 3, $y)

@mixin creature-dir-tile($x, $y)
  @include creature
  &.-down-creature
    &:after
      @include tile($x, $y)
  &.-left-creature
    &:after
      @include tile($x + 1, $y)
  &.-up-creature
    &:after
      @include tile($x + 2, $y)
  &.-right-creature
    &:after
      @include tile($x + 3, $y)

.level-row
  white-space: nowrap

  > .tile
    width: $cell-size
    height: $cell-size
    position: relative
    display: inline-block
    background: url(../images/tiles.png)
    background-size: $sprite-x * $sprite-ratio $sprite-y * $sprite-ratio

    &.-boy
      @include creature-dir-tile(0, 0)

    &.-swimming
      @include creature-dir-tile(4, 0)

    &.-drown
      @include tile(1, 3)

    &.-burned
      @include tile(7, 2)

    &.-clone-machine-button
      @include tile(4, 6)

    &.-tank-button
      @include tile(5, 6)

    &.-wall-button
      @include tile(6, 6)

    &.-trap-button
      @include tile(7, 6)

    &.-trap
      @include tile(1, 7)

    &.-bomb
      @include tile(6, 5)

    &.-thief
      @include tile(7, 5)

    &.-switchable-wall
      &.-on
        @include tile(4, 5)
      &.-off
        @include tile(5, 5)

    &.-force
      @include dir-tile(4, 4)

    &.-key
      &.-red
        @include tile(0, 5)
      &.-cyan
        @include tile(1, 5)
      &.-yellow
        @include tile(2, 5)
      &.-green
        @include tile(3, 5)

    &.-door
      &.-red
        @include tile(0, 4)
      &.-cyan
        @include tile(1, 4)
      &.-yellow
        @include tile(2, 4)
      &.-green
        @include tile(3, 4)

    &.-clone-machine
      @include tile(3, 7)

    &.-teleport
      @include tile(2, 7)

    &.-floor
      @include tile(0, 1)

    &.-wall
      @include tile(2, 1)
      &.-blue
        @include tile(6, 1)
      &.-recessed
        @include tile(0, 7)
      &.-down
        @include tile(3, 2)
      &.-right
        @include tile(4, 2)

    &.-ski-skates
      @include tile(0, 6)

    &.-suction-boots
      @include tile(1, 6)

    &.-fire-boots
      @include tile(2, 6)

    &.-flippers
      @include tile(3, 6)

    &.-chip
      @include tile(3, 1)

    &.-socket
      @include tile(4, 1)

    &.-exit
      @include tile(5, 1)

    &.-hint
      @include tile(1, 1)

    &.-water
      @include tile(0, 3)

    &.-dirt
      @include tile(1, 2)

    &.-gravel
      @include tile(2, 2)

    &.-block
      @include tile(0, 2)

    &.-fire
      @include tile(7, 1)

    &.-ice
      @include tile(6, 3)

      &.-corner
        &.-down
          @include tile(4, 3)
        &.-left
          @include tile(5, 3)
        &.-up
          @include tile(2, 3)
        &.-right
          @include tile(3, 3)

    &.-bee
      @include dir-tile(0, 8)

    &.-teeth
      @include dir-tile(0, 10)

    &.-ball
      @include tile(0, 9)

    &.-fireball
      @include tile(4, 11)

    &.-tank
      @include dir-tile(4, 8)

    &.-glider
      @include dir-tile(4, 9)
